<template>
  <div class="row">
   <div class="col-md-12">
      <alert 
        :alert="alert"
      />
   </div>
   <div class="col-md-12">
      <div class="row">
        <router-link 
          v-if="oas.length !== 0"
          class="btn btn-primary" 
          to="/oas/add"
        >
          Add new Oa
        </router-link>
        <SearchBox
          @handleSearch="globalSearchOa"
          @handleSelect="updateSearchType"
          :selectOptions="types"
          selected="events"
          selectable
        />
      </div>
      
      <div class="card" v-if="oas.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="oas" 
          :columns="table.columns"
          :actions="table.actions"
          @clickDelete="handleDeleteOa"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreOas"
          v-if="isMoreOasAvialiable || count > 10"
        >
          Show more
        </button>
      </div>
      <jumbotron 
        title="Ups nothing"
        text="Please add more Highlights"
        v-if="oas.length === 0"
      >
        <router-link 
          class="btn btn-primary" 
          to="/oas/add"
        >
          Add new oa
        </router-link>
      </jumbotron>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState, mapMutations } from 'vuex'
  import { tableColumns, tableActions, types } from './TableMock.js'

  export default {
    components: {
      PaperTable: () => import('ui/PaperTable.vue')
    },
    data: () => ({
      tableActions,
      tableColumns,
      types,
      table: {
        title: 'Oas list',
        subTitle: 'Oas existing list',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchOas()
    },
    watch: {
      oas(value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        oas: 'getOas',
        isMoreOasAvialiable: 'isMoreOasAvialiable'
      }),
      ...mapState({
        alert: state => state.alert,
        user: state => state.user,
        count: ({ oas: { pagination } }) => pagination.count
      })
    },
    methods: {
      handleDeleteOa(value) {
        const id = value
        this.removeOa({ id })
      },
      ...mapMutations([
        'updateSearchType'
      ]),
      ...mapActions([
        'fetchOas',
        'removeOa',
        'showMoreOas',
        'globalSearchOa'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }

  .alert {
    margin: 0px 0px 10px;
  }
</style> 



